import React from 'react'
import { Link, navigate } from 'gatsby'
import { Location } from '@reach/router'
import { determineLanguage } from 'components/DisplayLanguageDeterminer'

const lang = {
  'en': {
    'menu': 'Menu',
    'home': 'Home',
    'about_us': 'About Us',
    'gatherings': 'Gatherings',
    'sharings': 'Sharings',
    'giving': 'Giving',
    'gallery': 'Gallery',
    'live_stream': 'Live Stream'
  },
  'zh-Hans': {
    'menu': '菜单',
    'home': '首页',
    'about_us': '关于我们',
    'gatherings': '回家聚集',
    'sharings': '信息分享',
    'giving': '奉献',
    'gallery': '图集',
    'live_stream': '在线直播'
  },
  'zh-Hant': {
    'menu': '菜單',
    'home': '首頁',
    'about_us': '關於我們',
    'gatherings': '回家聚集',
    'sharings': '信息分享',
    'giving': '奉獻',
    'gallery': '圖集',
    'live_stream': '在線直播'
  }
}

const rootPathForDisplayedLanguage = {
  'en': '/',
  'zh-Hans': '/zh-Hans/',
  'zh-Hant': '/zh-Hant/'
}

export default function Nav({ onMenuToggle = () => {} }) {
  return (
    <Location>
      {
        ({ location }) => {
          const currentPath = location.pathname
          const displayedLanguage = determineLanguage({ path: currentPath })
          console.log(lang)
          console.log(displayedLanguage)
          console.log(lang[displayedLanguage])

          return (
            <nav id="nav">
              <ul>
                <li className="special">
                  <a
                    href="#menu"
                    onClick={e => {
                      e.preventDefault()
                      onMenuToggle()
                    }}
                    className="menuToggle"
                  >
                    <span>{lang[displayedLanguage]['menu']}</span>
                  </a>
                  <div id="menu">
                    <select
                      id='langSelect'
                      value={rootPathForDisplayedLanguage[displayedLanguage]}
                      onChange={
                        event => {
                          event.preventDefault()
                          navigate(document.getElementById('langSelect').value)
                        }
                      }>
                      <option key="en" value="/">English</option>
                      <option key="zh-Hans" value="/zh-Hans/">简体中文</option>
                      <option key="zh-Hant" value="/zh-Hant/">繁體中文</option>
                    </select>
                    <br/>

                    <ul>
                      <li>
                        <Link to={rootPathForDisplayedLanguage[displayedLanguage]} className="col-6">{lang[displayedLanguage]['home']}</Link>
                      </li>
                      <li>
                        <Link to={rootPathForDisplayedLanguage[displayedLanguage] + 'about'} className="col-6">{lang[displayedLanguage]['about_us']}</Link>
                      </li>
                      <li>
                        <Link to={rootPathForDisplayedLanguage[displayedLanguage] + 'gatherings'} className="col-6">{lang[displayedLanguage]['gatherings']}</Link>
                      </li>
                      <li>
                        <Link to={rootPathForDisplayedLanguage[displayedLanguage] + 'sharings'} className="col-6">{lang[displayedLanguage]['sharings']}</Link>
                      </li>
                      <li>
                        <Link to={rootPathForDisplayedLanguage[displayedLanguage] + 'giving'} className="col-6">{lang[displayedLanguage]['giving']}</Link>
                      </li>
                      <li>
                        <a href="https://www.flickr.com/photos/182245855@N04/albums" target="_blank" rel="noopener noreferrer" className="col-6">{lang[displayedLanguage]['gallery']}</a>
                      </li>
                      <li>
                        <a href="https://www.youtube.com/channel/UCOff0cemmvakSbkV4HhNzbA/live" target="_blank" rel="noopener noreferrer" className="col-6">{lang[displayedLanguage]['live_stream']}</a>
                      </li>
                    </ul>
                    <a
                      className="close"
                      onClick={
                        e => {
                          e.preventDefault()
                          onMenuToggle()
                        }
                      }
                      href="#menu"
                    >
                      {''}
                    </a>
                  </div>
                </li>
              </ul>
            </nav>
          )
        }
      }
    </Location>
  )
}
